
$min-width-xs: 576;

.app {
  display: flex;
  flex-direction: column;
  height: 100%;

  @media (min-width: 576px) {
    flex-direction: row;
  }

  &__column {
    width: 100%;
    height: 50%;

    @media (min-width: 576px) {
      width: 50%;
      height: 100%;
    }
  }
}

.video {
  width: 100%;
  height: 100%;
  background: #000;
  border: 8px solid #c8c8c8 ;
  box-sizing: border-box;
  position: relative;

  &__attendance,
  &__status {
    position: absolute;
    color: #fff;
    padding: 4px 8px;
    background-color: rgba(0, 0, 0, 0.7);
  }

  &__player {
    width: 100%;
    height: 100%;
  }

  &__footer {
    position: absolute;
    width: 100%;
    bottom: 4%;
    display: flex;
    justify-content: center;
  }

  &__switch,
  &__ready,
  &__next {
    background-color: #c8c8c8;
    border: none;
    padding: 8px;
    margin: 8px;
    font-size: 16px;
    cursor: pointer;
  }
}